:root .dara-form {
    --border-color: #dfe1e5;
    --color-danger: #d9534f;
    --background-danger: #d9534f;
    --font-color: #70757a;
    --invalid-font-color: #ff4136;
    --invalid-border-color: #ffb6b4;
    --invalid-background-color: #FDD;
    --background-button-hover: #ebebeb;
}

.dara-form {
    padding: 0px;
    color: var(--font-color);

    *,
    ::after,
    ::before {
        box-sizing: border-box;
    }

    .dara-form-field-container {
        .dara-form-field {
            position: relative;

            .file-wrapper {
                border: 1px solid var(--border-color);
                text-align: center;
                display: block;
                width: 100%;
                padding: 0.375rem 0.75rem;
                font-size: 1rem;
                font-weight: 400;
                line-height: 1.5;
                background-clip: padding-box;
                border-radius: 4px;
                transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
            }
        }
    }

    &.horizontal {
        margin: 0px 0px;
        display: table;
        width: 100%;

        >.dara-form-row {
            display: table-row;

            >.dara-form-label {
                width: 10%;
                display: table-cell;
            }

            >.dara-form-field-container {
                display: table-cell;
                padding-bottom: 10px;
            }

        }
    }

    &.vertical {
        >.dara-form-row>* {
            display: block;
        }
    }

    >.dara-form-row {
        width: 100%;
        margin: 0px 0px 10px 0px;

        >.dara-form-label {

            .require {
                &::after {
                    content: "*";
                    vertical-align: middle;
                }

                color: var(--color-danger);
            }
        }

        .help-message {
            display: none;
        }

        .help-icon {
            background-repeat: no-repeat;
            background-position-y: center;

            &.form-field {
                background-position-x: calc(100% - 15px);
            }

            &.dara-icon {
                display: none;
                position: absolute;
                z-index: 1;
                top: 0px;
                right: 0px;
                height: 100%;
                width: 20px;
                margin-right: 15px;
            }
        }

        &.invalid {
            .form-field {
                border-color: var(--invalid-border-color);
                outline-color: var(--invalid-border-color);
            }

            .help-icon {
                display: block;
                background-image: url('data:image/svg+xml; base64, ');
            }

            .help-message {
                display: block;
                color: var(--invalid-font-color);
            }
        }

        &.valid {
            .help-icon {
                display: block;
                background-image: url('data:image/svg+xml; base64, PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgLTk2MCA5NjAgOTYwIiB3aWR0aD0iMjAiIGhlaWdodD0iMjAiIHN0eWxlPSImIzEwOyAgICBmaWxsOiAjMTlhOTc0OyYjMTA7Ij48cGF0aCBkPSJNMzc4LTI0NiAxNTQtNDcwbDQzLTQzIDE4MSAxODEgMzg0LTM4NCA0MyA0My00MjcgNDI3WiIvPjwvc3ZnPg==');
            }
        }

        .dara-file-list {
            .file-icon {
                width: 20px;
                height: 20px;
                display: inline-block;
                vertical-align: middle;
                cursor: pointer;

                &:hover {
                    background-color: var(--background-button-hover);
                }

                &.download {
                    background-image: url("data:image/svg+xml;base64,");
                }

                &.remove {
                    background-image: url("data:image/svg+xml;base64,");
                }
            }

            .file-name {
                text-overflow: ellipsis;
                white-space: nowrap;
                word-wrap: normal;
                overflow: hidden;
                display: inline-block;
                vertical-align: middle;
                width: calc(100% - 55px);
            }
        }
    }


    .file-label {
        border: 1px solid var(--border-color);
        display: inline;
        width: 100%;
        padding: 3px 15px;
        line-height: 1;
        background-clip: padding-box;
        border-radius: 4px;
        transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;

        &:hover {
            background-color: var(--background-button-hover);
        }
    }

    .form-field {
        border: 1px solid var(--border-color);
        display: block;
        width: 100%;
        padding: 0.375rem 0.75rem;
        font-size: 1rem;
        font-weight: 400;
        line-height: 1.5;
        background-clip: padding-box;
        border-radius: 4px;
        transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;

        &[type=radio],
        &[type="checkbox"] {
            width: auto;
            display: inline;
        }

        &.dropdown {
            appearance: none;
            -webkit-appearance: none;
            -moz-appearance: none;
        }

        &.textarea {
            padding-right: 0px;
        }

        &[type="number"] {
            padding-right: 3px;
        }

        &.file {
            display: none;
        }

    }

    .field-group {
        .field {
            &.vertical {
                display: block;
            }

            &.horizontal {
                display: inline;
            }
        }
    }



}